* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-color: #112d49;
}
h1 {
  color: whitesmoke;
  border: 1px solid whitesmoke;
  border-radius: 10px;
  max-width: 279px;
  margin: 20px auto 20px;
  padding: 2px;
}
button {
  font-size: 22px;
  padding: 10px;
  border-radius: 10px;
  border: none;
}
.range {
  max-width: 284px;
  margin: 20px auto;
  margin-top: 20px;
  display: flex;
  gap: 20px;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
input {
  width: 100%;
}
.range-label {
  color: whitesmoke;
  font-size: 28px;
}
